<template>
    <cube-page type="swipe-view" title="我关注的" :isback="false">
        <template slot="content">
            <div class="swipe-wrapper" ref="scrollWrapper">
                <cube-scroll :options="options"
                             ref="contentScroll"
                             @pulling-down="onPullingDown">
                    <cube-swipe>
                        <transition-group name="swipe" tag="ul">
                            <li class="swipe-item-wrapper"
                                v-for="(data,index) in goodData"
                                :key="data.item.id"
                            >
                                <cube-swipe-item
                                        ref="swipeItem"
                                        :btns="data.btns"
                                        :index="index"
                                        @btn-click="onBtnClick"
                                        @active="onItemActive">
                                    <!--<a :href="data.item.url" style="position: relative;z-index: -1">-->
                                    <!--@click="go_buy(data.item.url)"-->
                                    <div class="good-flow" >
                                        <div class="good-img">
                                            <img :src="data.item.imgurl" alt="">
                                        </div>
                                        <div class="good-content">
                                            <p style="font-weight: 600">{{data.item.name}}</p>
                                            <p>当前价格:&nbsp;<span>${{data.item.price}}</span></p>
                                            <p>预期价格:&nbsp;<span>${{data.item.goal_price}}</span></p>
                                        </div>
                                    </div>
                                    <!--</a>-->
                                </cube-swipe-item>
                            </li>
                        </transition-group>
                    </cube-swipe>
                    <template slot="pulldown" slot-scope="props">
                        <div v-if="props.pullDownRefresh"
                             class="cube-pulldown-wrapper"
                             :style="props.pullDownStyle">
                            <div v-if="props.beforePullDown"
                                 class="before-trigger"
                                 :style="{paddingTop: props.bubbleY + 'px'}">
                                <span :class="{rotate: props.bubbleY > 0}" style="font-size: 0.2rem">↓</span>
                            </div>
                            <div class="after-trigger" v-else>
                                <div v-show="props.isPullingDown" class="loading">
                                    <cube-loading></cube-loading>
                                </div>
                            </div>
                        </div>
                    </template>
                </cube-scroll>
            </div>
        </template>
    </cube-page>
</template>
<script>
  import CubePage from '../../components/cube-page.vue'

  export default {
    data() {
      return {
        activeIndex: -1,
        goodData: [],
        options: {
          pullDownRefresh: {
            threshold: 60,
            // stop: 44,
            stopTime: 1000
            // txt: '更新成功'
          }
          // pullUpLoad: false
        },
        secondStop: 26
      }
    },
    created() {
      this.get_good()
      bus.$on('set_goalprice', () => {
        this.$nextTick(() => {
          this.get_good()
        })
      })
    },
    methods: {
      go_buy(url) {
        window.location.href = url
      },
      onPullingDown() {
        setTimeout(() => {
          this.get_good()
          this.$refs.contentScroll.forceUpdate()
        }, 1000)
      },
      get_good() {
        axios.get('goods/').then((response) => {
          this.goodData = []
          response.data.forEach(val => {
            this.goodData.push({
              item: val, btns: [
                {
                  action: 'edit',
                  text: '设置预期价格',
                  color: '#67C23A'
                }
              ]
            })
          })
        })
      },
      onItemClick(item) {
        // console.log('click item:', item)
      },
      onBtnClick(btn, index) {
        if (btn.action === 'edit') {
          this.goal = this.$createGoal({
            imgurl: this.goodData[index].item.imgurl,
            name: this.goodData[index].item.name
          })
          // console.log(this.goodData[index].item.name)
          this.goal.show()
          this.$refs.swipeItem[index].shrink()
        }
      },
      onItemActive(index) {
        if (index === this.activeIndex) {
          return
        }
        if (this.activeIndex !== -1) {
          const activeItem = this.$refs.swipeItem[this.activeIndex]
          activeItem.shrink()
        }
        this.activeIndex = index
      }
    },
    components: {
      CubePage
    }
  }
</script>
<style lang="less" scoped>
    .cube-scroll-wrapper {
        /*height: 93%;*/
    }
    .swipe-wrapper {
        position: fixed;
        /*z-index: -1;*/
        top: 55px;
        left: 10px;
        right: 10px;
        bottom: 0.50rem;
        max-height: 100%;
        background: #fff;
        .swipe-item-wrapper {
            overflow: hidden;
        }
    }

    .title {
        font-size: 0.2rem;
        /*text-align: center;*/
        background: #f0f0f0;
        border-bottom: 1px solid #d0d0d0;
        padding: .08rem 0 .08rem .2rem;
        /*margin-left: .2rem;*/
    }

    .good-flow {
        position: relative;
        padding: .14rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border-bottom: 1px solid #f0f0f0;

        .good-img {
            max-width: 1rem;
            max-height: 1rem;
            display: flex;
            flex-direction: row;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                max-width: 1rem;
                max-height: 1rem;
            }
            margin-right: .5rem;
        }
        .good-content {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            p {
                max-width: 2.5rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                font-size: 0.16rem;
                margin-bottom: .1rem;
                span {
                    font-size: 0.16rem;
                    color: #f01414;
                }
            }
        }

    }
</style>



