<template>
    <div class="subscribe-dialog-view">
        <cube-popup ref="popup" @mask-click="hide">
            <div class="subscribe-dialog-wrapper">
                <span class="close" @click="hide"><i class="cubeic-close"></i></span>
                <div class="content">
                    <cube-form :model="model" ref="cubeForm" @validate="validateHandler">
                        <cube-form-item :field="fields" style="font-size: .18rem"></cube-form-item>
                    </cube-form>

                    <p>{{name}}</p>
                    <img :src="imgurl" alt="">

                    <cube-button class="button" @click="start" :disabled="disabled">立即设置</cube-button>

                </div>
            </div>
        </cube-popup>
    </div>
</template>

<script>
  export default {
    name: 'goal',
    props: {
      name: '',
      imgurl: ''
    },
    data() {
      return {
        disabled: true,
        model: {
          inputValue: ''
        },
        fields: {
          type: 'input',
          modelKey: 'inputValue',
          props: {
            placeholder: '请输入预期价格'
          },
          rules: {
            type: 'number',
            required: true
          },
          message: {}
        }
      }
    },
    methods: {
      validateHandler(result) {
        console.log(result.valid)
        this.disabled = !result.valid
      },
      show() {
        this.$refs.popup.show()
        this.$emit('show')
      },
      hide() {
        this.$refs.popup.hide()
        this.$emit('hide')
        this.$refs.cubeForm.reset()
      },
      start() {
        let data = {}
        data.name = this.name
        data.goal_price = this.model.inputValue
        axios.put('goods/', data).then((res) => {
          bus.$emit('set_goalprice')
        })
        this.$refs.popup.hide()
        this.$refs.cubeForm.reset()
        this.$createToast({
          type: 'correct',
          txt: '设置成功',
          time: 500
        }).show()
      }
    }
  }
</script>

<style lang="stylus">
    .subscribe-dialog-wrapper
        position relative
        box-sizing: border-box
        max-width: 100%
        margin 0 .2rem
        padding 0.4rem 0.2rem 0.2rem 0.2rem
        background-color: #F6F6F6
        border-radius: 4px
        .close
            position: absolute
            top: 0.1rem
            right: 0.1rem
            font-size: 0.25rem
            color: #A2A8AF
        .title
            font-size: 17px
            color: #222C36
        .desc
            line-height: 20px
            font-size: 13px
            color: #C0C8D1
        .button
            display: inline-block
            width: 230px
            margin-top: 20px
            padding: 14px
            border-radius: 4px
            font-size: 15px
        .content {
            max-width 100%;
            min-height 4rem;
            .cube-form {
                width 100%;
                margin .1rem 0;
            }
            display flex
            justify-content center
            align-items center
            flex-direction column
            img {
                max-width 3.4rem;
                max-height 4rem;
            }
            p {
                width: 100%;
                font-size: 0.16rem;
                margin-bottom: .1rem;
                line-height 1.3;
                font-weight 600;
            }
        }
</style>




