<template>
    <cube-page type="swipe-view" title="我的" :isback="false">
        <template slot="content">
            <div class="me-mainer">
                <div class="user">
                    <img :src="logourl" alt="">
                    <p>{{username}}</p>
                </div>

                <router-link tag="div" to="/home/me/setting" class="options">
                    <i class="cubeic-setting"></i>
                    <h1>设置</h1>
                </router-link>
            </div>
        </template>
    </cube-page>
</template>
<script>
  import CubePage from '../../components/cube-page.vue'
  import hj from './hj.png'

  export default {
    data() {
      return {
        logourl: hj,
        username: ''
      }
    },
    methods:{
      get_username(){
        this.username = Lockr.get('userInfo').username
      }
    },
    created() {
    },
    components: {
      CubePage
    },
    watch: {
      '$route'(to, from) {
        // console.log(from.path)
        if(from.path == '/login'){
          this.get_username()
          // console.log(this.get_username())
        }
      }
    }
  }

</script>
<style lang="less" scoped>

    .me-mainer {
        padding: 0.2rem 0.1rem 0 0.1rem;
        background: #fff;
        display: flex;
        flex-direction: column;
        /*justify-content: center;*/
        .user {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            img {
                width: 0.5rem;
                height: 0.5rem;
                border-radius: 100%;
                border: 1px solid #262626;
                margin-bottom: 0.1rem;
            }
            p {
                font-size: 0.25rem;
            }

        }
        .options {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin: 0.2rem 0;
            min-height: 0.4rem;
            background: #262626;
            border-radius: 0.05rem;
            h1 {
                font-size: 0.18rem;
                color: #fff;
            }
            i {
                font-size: 0.18rem;
                margin-right: 0.1rem;
                color: #fff;
            }
        }
    }

</style>



