import Vue from 'vue'
import App from './App'
import 'babel-polyfill'
import axios from 'axios'
import Lockr from 'lockr'
import Cookies from 'js-cookie'
import _ from 'lodash'
import moment from 'moment'
import routes from './routes'
import VueRouter from 'vue-router'
import store from './vuex/store'
import cube from 'cube-ui'
import VueResource from 'vue-resource'
import { createAPI } from 'cube-ui'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

axios.defaults.baseURL = 'http://47.52.139.40/'
axios.defaults.timeout = 1000 * 15

axios.defaults.headers.Authorization = 'JWT ' + (Lockr.get('token') ? Lockr.get('token') : '')
// axios.defaults.headers.Authorization = 'JWT eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyX2lkIjoyLCJ1c2VybmFtZSI6ImMiLCJleHAiOjE1MzI2NDQ2MjEsImVtYWlsIjpudWxsfQ.4Ct1y6jmNCsvbAt45kVa_tdueSrWTIAvLbM1a2SPvVo'
// axios.defaults.headers.authKey = Lockr.get('authKey')
// axios.defaults.headers.sessionId = Lockr.get('sessionId')
axios.interceptors.response.use((response) => {
// 对响应数据做点什么
  return response
}, (err) => {
  return Promise.reject(err.response.data)
})
axios.defaults.headers['Content-Type'] = 'application/json'
const router = new VueRouter({
  mode: 'hash',
  base: '',
  routes
})

router.beforeEach((to, from, next) => {
  // NProgress.start()
  // axios.get('islogin/').then((res) => {
  //   next()
  // }).catch(error => {
  //   this.$createToast({
  //     type: 'error',
  //     txt: '您还未登录或者登录信息已过期',
  //     time: 1500
  //   }).show()
  //   next({path:'/login'})
  // })
  next()
})

router.afterEach(transition => {
  // NProgress.done()
})

import Goal from './components/edit/goalprice'

createAPI(Vue, Goal, [], true)
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(cube)
Vue.config.productionTip = false
window.router = router
window.store = store
window.HOST = 'http://47.52.139.40/'
window.axios = axios
window._ = _
window.moment = moment
window.Lockr = Lockr
window.Cookies = Cookies
window.pageSize = 15
window.addEventListener('plusready', function () {
  message = document.getElementById('message')
// 监听点击消息事件
  plus.push.addEventListener('click', function (msg) {
// 判断是从本地创建还是离线推送的消息
    switch (msg.payload) {
      case 'LocalMSG':
        outSet('点击本地创建消息启动：')
        break
      default:
        outSet('点击离线推送消息启动：')
        break
    }
// 提示点击的内容
    plus.ui.alert(msg.content)
// 处理其它数据
    logoutPushMsg(msg)
  }, false)
// 监听在线消息事件
  plus.push.addEventListener('receive', function (msg) {
    if (msg.aps) {  // Apple APNS message
      outSet('接收到在线APNS消息：')
    } else {
      outSet('接收到在线透传消息：')
    }
    logoutPushMsg(msg)
  }, false)
}, false)
const bus = new Vue()
window.bus = bus
new Vue({
  el: '#app',
  template: '<App/>',
  store,
  router,
  components: { App }
}).$mount('#app')



