<template>
    <div slot="content">
        <!-- showSlider -->
        <div class="section">
            <cube-tab-bar
                    v-model="selectedLabelSlots"
                    showSlider
                    @click="clickHandler"
                    @change="changeHandler">
                <cube-tab v-for="(item, index) in tabs" :icon="item.icon" :label="item.label" :key="item.label">
                </cube-tab>
            </cube-tab-bar>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import CubePage from '../../components/cube-page.vue'

  export default {
    data() {
      return {
        selectedLabelSlots: '商品',
        tabs: [{
          label: '商品',
          icon: 'cubeic-home',
          path: '/home/index'
        }, {
          label: '搜索',
          icon: 'cubeic-like',
          path: '/home/search'

        }, {
          label: '消息',
          icon: 'cubeic-vip',
          path: '/home/news'

        }, {
          label: '我的',
          icon: 'cubeic-person',
          path: '/home/me'

        }]
      }
    },
    methods: {
      clickHandler(label) {
        this.tabs.forEach((val) => {
          if (label === val.label) {
            router.push({ path: val.path })
            return
          }
        })
      },
      changeHandler(label) {
        // console.log('value has changed, now is', label)
      }
    },
    components: {
      CubePage
    },
    watch: {
      selectedLabel(newV) {
        console.log(newV)
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/variable.styl"
    .cube-tab-bar {
        position: absolute;
        background: #f0f0f0;
        z-index: 40;
        bottom: 0;
        width: 100%;
        border-top: 1px solid #d0d0d0;
        font-size: 0.17rem;
    }

    .cube-tab {
        position: relative;
        z-index: 50;
    }

    .tab-bar-view
        .section
            margin-bottom: 10px
            background-color: white
        .option-list
            .group
                margin-bottom: 15px
            .item
                height: 52px
                border: 1px solid rgba(0, 0, 0, .1)
                background-color: white
                border-radius: 5px
        .cube-tab-bar
            background-color: white
</style>






