<template>
    <cube-page type="swipe-view" title="设置">
        <template slot="content">
            <cube-button @click="showBtn" class="options">退出登录</cube-button>
        </template>
    </cube-page>
</template>
<script>
  import CubePage from '../../components/cube-page.vue'

  export default {
    data() {
      return {
        username: ''
      }
    },
    created() {
      this.username = Lockr.get('userInfo').username
    },
    methods: {
      showBtn() {
        this.$createDialog({
          type: 'confirm',
          icon: 'cubeic-warn',
          title: '确定退出吗',
          // content: '我是内容',
          confirmBtn: {
            text: '确定',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            this.logout()
            this.$createToast({
              type: 'warn',
              time: 1000,
              txt: '退出登录成功'
            }).show()

          }
        }).show()
      },
      logout() {

        Lockr.rm('token')
        Lockr.rm('userInfo')
        window.axios.defaults.headers.Authorization = ''
        setTimeout(() => {
          router.replace('/login')
        }, 1000)
      }
    },
    components: {
      CubePage
    }
  }

</script>
<style lang="less" scoped>

    .options {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 0.2rem 0;
        min-height: 0.4rem;
        background: #f01414;
        border-radius: 0.05rem;
        h1 {
            font-size: 0.18rem;
            color: #fff;
        }
        i {
            font-size: 0.18rem;
            margin-right: 0.1rem;
            color: #fff;
        }
    }

</style>



