import Home from './components/Home.vue'
import Login from './components/Account/Login.vue'
import index from './components/main/index'
import search from './components/main/search'
import news from './components/main/news'
import me from './components/main/me'
import setting from './components/me_setting/me_setting'

const routes = [
  { path: '/', redirect: '/home' },
  { path: '/login', component: Login, name: 'Login' },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/', redirect: '/home/index' },
      { path: '/home/index', component: index, name: 'index' },
      { path: '/home/search', component: search, name: 'search' },
      { path: '/home/news', component: news, name: 'news' },
      {
        path: '/home/me', component: me, name: 'me',
        children: [

        ]
      }
    ]
  },
  { path: '/home/me/setting', component: setting, name: 'setting' }
]
export default routes



