<template>
    <cube-page type="swipe-view" title="登录" :isback="false">
        <template slot="content">
            <cube-form :model="model" ref="cubeForm" @validate="validateHandler">
                <cube-form-item :field="fields[0]" style="font-size: .18rem;"></cube-form-item>
                <cube-form-item :field="fields[1]" style="font-size: .18rem"></cube-form-item>
            </cube-form>
            <cube-button type="button" @click="login" :disabled="disabled" style="margin-top: 0.1rem">登录</cube-button>
        </template>
    </cube-page>
</template>
<script>
  import CubePage from '../../components/cube-page.vue'

  export default {
    data() {
      return {
        disabled: true,
        model: {
          username: '',
          password: ''
        },
        fields: [{
          type: 'input',
          modelKey: 'username',
          props: {
            placeholder: '请输入用户名'
          },
          rules: {
            type: 'string',
            required: true
          },
          message: {}
        }, {
          type: 'input',
          modelKey: 'password',
          props: {
            placeholder: '请输入密码',
            type: 'password',
            eye: false
          },
          rules: {
            type: 'string',
            required: true
          },
          message: {}
        }]
      }
    },
    created() {
    },
    methods: {
      login() {
        let data = {}
        data.username = this.model.username
        data.password = this.model.password
        // console.log(data)
        axios.post('login/', data).then((res) => {
          // console.log(res.data)
          Lockr.set('userInfo', res.data.user)            // 用户信息
          Lockr.set('token', res.data.token)              // 权限认证
          window.axios.defaults.headers.Authorization = 'JWT ' + Lockr.get('token')
          setTimeout(() => {
            router.push({ 'path': '/home/me' })
          }, 1500)
          this.$createToast({
            type: 'correct',
            txt: '登录成功',
            time: 1500
          }).show()
        }).catch(error => {          //  axios 配置里必须要 return Promise.reject(error.response.data)
          console.log(error)
          this.$createToast({
            type: 'error',
            txt: '用户名或密码错误',
            time: 1500
          }).show()
        })
      },
      validateHandler(result) {
        // console.log(result.valid)
        this.disabled = !result.valid
      }
    },
    components: {
      CubePage
    }
  }
</script>
<style lang="less">
</style>



