<template>
    <cube-page type="swipe-view" title="搜索" :isback="false">
        <template slot="content">
            <h1 style="font-size: 0.2rem;text-align: center">正在开发中！</h1>
            <cube-button @click="getPushInfo">获取用户标识</cube-button>
            <cube-button @click="clearAllpush">清空</cube-button>

        </template>
    </cube-page>
</template>
<script>
  import CubePage from '../../components/cube-page.vue'

  export default {
    data() {
      return {}
    },
    components: {
      CubePage
    },
    methods: {
      getPushInfo() {
        let info = plus.push.getClientInfo()
        alert(JSON.stringify(info))
      },
      clearAllpush() {
        plus.push.clear()
        alert('清空所有推送消息成功！')
      }
    }
  }
</script>
<style>
</style>



